  .box ul,
  .box ol,
  .box div {
    padding: 0.22rem 1rem 1.5rem; }
  .box h1 {
    padding: 20px;
    margin-bottom: 20px;
    text-transform: uppercase;
    background-color: #6c5ce7;
    color: #fff; }
  .box h2 {
    padding: 12px;
    text-transform: uppercase;
