/* button */
button {
  display: block;
  margin-bottom: 20px;
  /*display, margin 지우기*/
  border: 0 none;
  font-weight: 400;
  background-color: #b2bec3;
  color: #333;
  cursor: pointer;
  text-align: center;
  -moz-box-pack: start;
  -webkit-box-pack: start;
  -moz-box-align: center;
  -webkit-box-align: center;
  font-size: 1rem;
  line-height: 1.5;
  padding: .38rem .86rem;
  transition: 0.4s cubic-bezier(0.4, 0, 0.2, 1);
