.cursor {
  position: fixed;
  left: 0;
  top: 0;
  pointer-events: none; }

.cursor--small {
  width: 5px;
  height: 5px;
  left: -2.5px;
  top: -2.5px;
  border-radius: 50%;
  z-index: 11000;
  background: var(--color-text); }

.cursor--canvas {
  width: 100vw;
  height: 100vh;
  z-index: 12000; }

.cursor-wrapper {
  position: fixed;
  opacity: 1;
  width: 100px;
  height: 100px;
  left: -50px;
  top: -50px;
  transform: translate(-100%, -100%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1600000;
  pointer-events: none; }
  .cursor-wrapper.has-blend-mode {
    mix-blend-mode: exclusion; }

.custom-cursor {
  position: relative;
  left: 0;
  top: 0; }
  .custom-cursor__outer {
    width: 6px;
    height: 6px;
    border: 2px solid white;
    background: white;
    border-radius: 50%; }
  .custom-cursor__inner {
    position: relative;
    width: 100%;
    height: 100%; }
    .custom-cursor__inner:before, .custom-cursor__inner:after {
      content: "";
      position: absolute;
      width: 40%;
      height: 1px;
      background: white;
      transition: transform 0.1s linear; }
    .custom-cursor__inner:before {
      left: 50%;
      top: 50%;
      transform: translateX(-50%); }
    .custom-cursor__inner:after {
      left: 50%;
      top: 50%;
      transform: translateX(-50%) rotate(-90deg); }
    .custom-cursor__inner.is-closing:after {
      transform: translateX(-50%) rotate(0deg); }

